// src/App.js
import React, { useState, useEffect } from 'react';
import { BrowserRouter as Router, Routes, Route, Navigate } from 'react-router-dom';
import Login from './components/Login';
import SignUp from './components/SignUp'; // SignUp  import
import MoldList from './components/MoldList';
import MoldSearch from './components/MoldSearch';
import MoldCreate from './components/MoldCreate';
import MoldEdit from './components/MoldEdit';
import { supabase } from './supabaseClient';
import StatisticsView from './components/StatisticsView'; // StatisticsView import

function App() {
    const [session, setSession] = useState(null);

    useEffect(() => {
        supabase.auth.getSession().then(({ data: { session } }) => {
            setSession(session);
        });

        supabase.auth.onAuthStateChange((_event, session) => {
            setSession(session);
        });
    }, []);

    return (
        <Router basename="/mold">
            <Routes>
                <Route path="/" element={<Navigate to="/login" replace />} /> {/*    /login  */}
                <Route path="/login" element={<Login />} /> {/* /login  Login   */}
                <Route path="/signup" element={<SignUp />} /> {/*    */}
                <Route path="/mold-list" element={session ? <MoldList /> : <Navigate to="/login" replace />} /> {/*    /login  */}
                <Route path="/mold-search" element={session ? <MoldSearch /> : <Navigate to="/login" replace />} />
                <Route path="/mold-create" element={session ? <MoldCreate /> : <Navigate to="/login" replace />} />
                <Route path="/mold-edit/:id" element={session ? <MoldEdit /> : <Navigate to="/login" replace />} />
                <Route path="/statistics" element={session ? <StatisticsView /> : <Navigate to="/login" replace />} /> {/*     */}
            </Routes>
        </Router>
    );
}

export default App;