// src/components/MoldCreate.js

import React, { useState } from 'react';
import { supabase } from '../supabaseClient';
import { useNavigate } from 'react-router-dom';

function MoldCreate() {
    const [moldId, setMoldId] = useState('');
    const [status, setStatus] = useState('');
    const [status_date, setstatus_date] = useState('');
    const [inspectionStatus, setInspectionStatus] = useState('');
    const [inspector, setInspector] = useState('');
    const [moldCount, setMoldCount] = useState(0);
    const [UnitID, setUnitId] = useState('');
    const navigate = useNavigate();
    const [error, setError] = useState(null);

    const handleSubmit = async (e) => {
        e.preventDefault();
        const { error: insertError } = await supabase.from('molds').insert([
            {
                mold_id: moldId,
                status: status,
                status_date: status_date,
                inspection_status: inspectionStatus,
                inspector: inspector,
                mold_count: moldCount,
                unit_id: UnitID, //  UnitID 
            },
        ]);
        if (insertError) {
            console.error('  :', insertError);
            setError(insertError.message);
        } else {
            navigate('/mold-list');
        }
    };

    return (
        <div>
            <h2> </h2>
            {error && <div style={{ color: 'red' }}>{error}</div>}
            <form onSubmit={handleSubmit}>
                <div>
                    <label htmlFor="moldId"> ID:</label>
                    <input
                        type="text"
                        placeholder=" ID"
                        value={moldId}
                        onChange={(e) => setMoldId(e.target.value)}
                        id="moldId"
                        name="moldId"
                    />
                </div>
                <div>
                    <label htmlFor="status"> :</label>
                    <select value={status} onChange={(e) => setStatus(e.target.value)} id="status" name="status">
                        <option value="Mounted"></option>
                        <option value="Received"></option>
                        <option value="Shipped"></option>
                        {/*     */}
                    </select>
                </div>
                <div>
                    <label htmlFor="status_date"> /:</label>
                    <input
                        type="datetime-local"
                        value={status_date}
                        onChange={(e) => setstatus_date(e.target.value)}
                        id="status_date"
                        name="status_date"
                    />
                </div>
                <div>
                    <label htmlFor="inspectionStatus"> :</label>
                    <select value={inspectionStatus} onChange={(e) => setInspectionStatus(e.target.value)}
                        id="inspectionStatus" name="inspectionStatus">
                        <option value="WAITING"></option>
                        <option value="PASS"></option>
                        <option value="FAIL"></option>
                        {/*      */}
                    </select>
                </div>
                <div>
                    <label htmlFor="inspector">:</label>
                    <input
                        type="text"
                        placeholder=""
                        value={inspector}
                        onChange={(e) => setInspector(e.target.value)}
                        id="inspector"
                        name="inspector"
                    />
                </div>
                <div>
                    <label htmlFor="moldCount"> :</label>
                    <input
                        type="number"
                        placeholder=" "
                        value={moldCount}
                        onChange={(e) => setMoldCount(parseInt(e.target.value, 10) || 0)}
                        id="moldCount"
                        name="moldCount"
                    />
                </div>
                <div>
                    <label htmlFor="UnitID"> :</label>
                    <input
                        type="text"
                        placeholder=" "
                        value={UnitID}
                        onChange={(e) => setUnitId(e.target.value)}
                        id="UnitID"
                        name="UnitID"
                    />
                </div>
                <button type="submit"></button>
            </form>
        </div>
    );
}

export default MoldCreate;
